import React, { useMemo, useCallback } from 'react';
import {
  IconAlignCenter,
  IconAlignLeft,
  IconAlignRight,
  IconAlignJustify,
} from '../icons';

import { IconSelect, IconSelectSingleLineDefaultWidth } from './Basics';

export type HorizontalAlign = 'left' | 'center' | 'right' | 'justify';

export interface IHorizontalAlignSelectProps
  extends Omit<React.ComponentProps<typeof IconSelect>,
  'onChange' | 'options' | 'overlayVisible' | 'onVisibleChange' | 'width' | 'value'> {
  /**
   * 对齐方式修改时的回调函数
   */
  onChange: (align: HorizontalAlign) => void;
  /**
   * 选项的tooltip提示
   */
  options: Record<HorizontalAlign, { title: string, icon?: React.ReactElement }>;

  value: HorizontalAlign;
}

interface ISelectOption {
  title: string;
  key: string;
  value: HorizontalAlign;
  icon: React.ReactElement;
}

export const HorizontalAlignSelect: React.FC<IHorizontalAlignSelectProps> = (props) => {
  const {
    onChange,
    options,
    role,
    value,
    tooltip,
    ...rest
  } = props;

  // 垂直对齐的选项
  const normalizeOptions: ISelectOption[] = useMemo(
    () => [
      {
        title: options.left.title,
        value: 'left',
        key: 'left',
        icon: options.left.icon || <IconAlignLeft />,
        testid: 'option-left',
      },
      {
        title: options.center.title,
        value: 'center',
        key: 'center',
        icon: options.center.icon || <IconAlignCenter />,
        testid: 'option-center',
      },
      {
        title: options.right.title,
        value: 'right',
        key: 'right',
        icon: options.right.icon || <IconAlignRight />,
        testid: 'option-right',
      },
      {
        title: options.justify.title,
        value: 'justify',
        key: 'justify',
        icon: options.justify.icon || <IconAlignJustify />,
        testid: 'option-justify',
      },
    ],
    [options]
  );



  const handleChange = useCallback(
    (e) => {
      if (onChange) {
        onChange(e);
      }
    },
    [onChange]
  );

  return (
    <IconSelect
      value={value}
      onChange={handleChange}
      testid='HorizontalAlignSelect'
      tooltip={tooltip}
      role={role}
      options={normalizeOptions}
      width={IconSelectSingleLineDefaultWidth}
      {...rest}
    />
  );
};

